img {
  border-radius: 200px;
  width: 100px;
  height: 100px;
  border: 1px solid blueviolet;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

h2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
  color: white;
  text-align: center;
}

.main {
  background-image: linear-gradient(to right, #141e30, #243b55);
  margin: 0 auto;
  padding: 25px 0;
  min-width: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.links {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 280px;
  
}

.links > a {
  border-radius: 10px;
  margin: 10px;
  background-color: #087908;
  color: white;
  padding: 15px 25px;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  width: 200px;
  
}
a:hover {
  background-color: #4c5eeb;
}

div.socials {
  border-radius: 100%;
  padding: 10px;
  margin: 15px;
}